import { FormEvent, useState } from 'react';
import { Envelope, Lock } from 'phosphor-react';
import { Button } from '../components/Button';
import { Checkbox } from '../components/Checckbox';
import { Text } from '../components/Text';
import { TextInput } from '../components/TextInput';
import Logo from '../Logo';
import { Heading } from '../components/Heading';

export function SignIn() {
    const [isUserSingnedIn, setIsUserSingnedIn] = useState(false)

    function handleSignin(event: FormEvent) {
        event.preventDefault();

        setIsUserSingnedIn(true)
    }

    return (
        <div className='w-screen h-screen bg-gray-900 flex flex-col items-center justify-center text-gray-100'>
            <header className='flex flex-col items-center'>
                <Logo />
                <Heading size='lg' className='mt-4'>Ignite Lab</Heading>
                <Text size='lg' className='text-gray-400 -mt-1'>Faça login e comece a usar!</Text>
            </header>
            <form onSubmit={handleSignin} className='flex flex-col gap-4 items-stretch w-full max-w-sm mt-4' >
                {isUserSingnedIn && <Text>Login realizado!</Text>}
                <label htmlFor="email" className='flex flex-col gap-3' >
                    <Text className='font-semibold'>Endereço de e-mail</Text>
                    <TextInput.Root>
                        <TextInput.Icon>
                            <Envelope />
                        </TextInput.Icon>
                        <TextInput.Input type='email' id='email' placeholder='Digite seu e-mail' />
                    </TextInput.Root>
                </label>

                <label htmlFor="senha" className='flex flex-col gap-3' >
                    <Text className='font-semibold'>Sua Senha.</Text>
                    <TextInput.Root>
                        <TextInput.Icon>
                            <Lock />
                        </TextInput.Icon>
                        <TextInput.Input type='password' id='password' placeholder='*********' />
                    </TextInput.Root>
                </label>

                <label htmlFor='remember' className='flex items-center gap-2'>
                    <Checkbox id='remember' />
                    <Text size='sm' className='text-gray-200'>Lembrar de mim por 30 dias</Text>
                </label>

                <Button type='submit' className='mt-4'>Entrar na plataforma</Button>
            </form>

            <footer className='flex flex-col items-center gap-2 mt-4 mb-2'>
                <Text asChild size='sm'>
                    <a href="" className='text-gray-400 underline hover:text-gray-200'>Esqueceu sua senha?</a>
                </Text>
                <Text asChild size='sm'>
                    <a href="" className='text-gray-400 underline hover:text-gray-200'>Não possui conta? Crie uma agora!</a>
                </Text>
            </footer>
        </div>
    )
}
